.card {
	width: 31rem;
	background-color: white;
	margin: 0 auto;
	padding: 0 3rem 2rem 3rem;
}

.homeContainer {
	width: 31rem;
	text-align: center;
}

/* logout button */
.button-23 {
	background-color: #ffffff;
	border: 1px solid #222222;
	border-radius: 8px;
	box-sizing: border-box;
	color: #222222;
	cursor: pointer;
	display: inline-block;
	font-family: Circular, -apple-system, BlinkMacSystemFont, Roboto, "Helvetica Neue", sans-serif;
	font-size: 16px;
	font-weight: 600;
	line-height: 20px;
	margin: 2rem 0 0 0;
	outline: none;
	padding: 10px 20px;
	position: relative;
	text-align: center;
	text-decoration: none;
	touch-action: manipulation;
	transition: box-shadow .2s, -ms-transform .1s, -webkit-transform .1s, transform .1s;
	user-select: none;
	-webkit-user-select: none;
	width: auto;
}

.button-23:focus-visible {
	box-shadow: #222222 0 0 0 2px, rgba(255, 255, 255, 0.8) 0 0 0 4px;
	transition: box-shadow .2s;
}

.button-23:active {
	background-color: #f7f7f7;
	border-color: #000000;
	transform: scale(.96);
}

.button-23:disabled {
	border-color: #dddddd;
	color: #dddddd;
	cursor: not-allowed;
	opacity: 1;
}

.form {
	width: 23rem;
	margin: 0 auto;
	display: flex;
}

.input {
	height: 2.8rem;
	padding-left: .9rem;
	outline: none;
	border: 2px solid rgb(41, 41, 41);
	border-right: none;
	font-size: 1.2rem;
}

.add-todo-btn {
	width: 7rem;
	cursor: pointer;
	background-color: rgb(15, 15, 15);
	color: white;
	font-size: 1.2rem;
	outline: none;
	border: 2px solid rgb(41, 41, 41);
}

@media only screen and (max-width: 481px) {
	.card {
		width: 100vw;
		/* height: 100vh; */
		min-height: 100vh;
		margin: 0;
		padding: 0;
	}

	.homeContainer {
		width: 100%;
	}
}
