body {
	font-family: 'Noto Sans', sans-serif;
}

.container {
	width: 31rem;
	margin: 3rem auto 0 auto;
}

.todo {
	user-select: none;
	margin-bottom: 1rem;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

input[type=checkbox] {
	display: none;
}

input[type=checkbox] + label {
	color: #212121;
	font-size: 1.5rem;
	cursor: pointer;
	position: relative;
	border-radius: 3px;
	display: inline-block;
	padding: 5px 5px 5px 30px;
}

input[type=checkbox] + label:hover {
	color: #494949;
	background-color: #e1e1e1;
}

input[type=checkbox] + label span {
	left: 5px;
	top: 50%;
	position: absolute;
	transform: translateY(-50%);
	/* width: 15px; */
	/* height: 15px; */
	width: 1rem;
	height: 1rem;
	display: block;
	background: white;
	border-radius: 3px;
	border: 1px solid #ccd6e7;
	box-shadow: 0 2px 3px #f0f4f8;
}

input[type=checkbox]:checked + label {
	color: #aeb7c6;
	text-decoration: line-through;
	text-decoration-thickness: 2px;
}

input[type=checkbox]:checked + label span {
	background-color: transparent;
	border-color: transparent;
	box-shadow: none;
}

input[type=checkbox] + label span::after {
	width: 100%;
	height: 100%;
	content: '';
	display: block;
	position: absolute;
	background-image: url('https://s3-eu-west-1.amazonaws.com/thomascullen-codepen/tick.svg');
	/* background-image: url('check4.png'); */
	background-repeat: no-repeat;
	background-position: center;
	background-size: 17px 12px;
	/* background-size: 3rem 3rem; */
	transform: scale(0);
	transition: transform 300ms cubic-bezier(0.3, 0, 0, 1.5);
}

input[type=checkbox]:checked + label span::after {
	transform: scale(1);
}

input[type=checkbox] + label span::before {
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: block;
	content: '';
	position: absolute;
	border-radius: 50%;
	background: #8798aa;
	opacity: .8;
	transform: scale(0);
}

input[type=checkbox]:checked + label span::before {
	opacity: 0;
	transform: scale(1.3);
	transition: opacity 300ms cubic-bezier(0.2, 0, 0, 1),
		transform 400ms cubic-bezier(0.3, 0, 0, 1.4);
}

.deleteIconContainer {
	cursor: pointer;
}

.deleteIcon {
	width: 2rem;
}

@media only screen and (max-width: 481px) {
	input[type=checkbox] + label {
		font-size: 1.2rem;
	}

	.container {
		width: 100%;
		margin: 2.5rem auto 0 auto;
	}

	.todo {
		width: 85%;
		margin: 0 auto 1.1rem auto;
	}
}
